




*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    transition: all .2s;
}

.hbody{
    background: rgb(2, 2, 2,.9);
    width: 100%;
    height: 100vh;
    display: flex;
    transition: all .5s;
    overflow: hidden;
    padding: 10px;
    justify-content: center;
    align-items: center;
    color:white;
}
.Image{
    min-width: 25%;
    min-height: 92%;
    border: 1px solid rgb(0, 0, 0,.7);
    border-radius: 5px;
    margin-right: 10px;
    background: url(./porto.jpeg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
.Main_side{
    min-width: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    min-height: 95%;
    /* box-shadow: 4px 4px 8px rgb(226, 217, 217,.5); */
    overflow: hidden;
    border-radius: 5px;
    /* border-left: 1px solid rgb(236, 227, 227,.2); */
    /* border-top: 1px solid rgb(236, 227, 227,.2); */

    /* border-right: 1px solid rgb(236, 227, 227,.5); */
    /* border-bottom: 1px solid rgb(236, 227, 227,.5); */
}

#ih_4{
    color: white;
    font-size: 30px;
}

#ih_1{
    color: white;
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 45px;
    text-transform: capitalize;
}

#ih_2{
    color: white;
    font-size: 38px;
    text-transform: capitalize;
}

#ih_4{
    color: white;
    font-size: 25px;
}
.intro{
    min-width:45% ;
    /* min-height:45% ; */
    margin: 10px;
    display: flex;
        border-radius: 5px;
    border: 1px solid rgb(246, 241, 241,.15);
    justify-content: center;
    align-items: center;
         transition: all .5s linear;
    flex-direction: column;
    /* border: 1px solid rgba(44, 43, 43, 0.9); */
     height: 350px;
}

.myproj{
    min-width:45% ;
    margin: 10px;
    display: flex;
    justify-content: center;
    border-radius: 5px;
    /* border: 1px solid rgb(246, 241, 241,.15); */
    align-items: center;
    flex-direction: column;
    padding: 10px;
     transition: all .3s linear;
    /* min-height:45% ; */
    /* border: 1px solid white; */
     background: rgba(62, 61, 61, 0.3);
     height: 350px;
     max-height: 98%;

}

.getinfo{
    min-width:45% ;
    margin: 10px;
    display: flex;
        border-radius: 5px;
    /* border: 1px solid rgb(246, 241, 241,.15); */
    justify-content: center;
    align-items: center;
     /* font-size: 22px; */
    flex-direction: column;
     transition: all .3s linear;
     background: rgba(62, 61, 61, 0.3);
    /* border: 1px solid white; */
     height: 350px;
    /* min-height:45% ; */
}



.about{
    min-width:45% ;
    margin: 10px;
    display: flex;
        border-radius: 5px;
    /* border: 1px solid rgb(246, 241, 241,.15); */
    transition: all .3s linear;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: rgba(62, 61, 61, 0.3);
    height: 350px;
    /* min-height:45% ; */
    /* border: 1px solid white; */
}



@media (max-width:740px){
     .Image{
          min-width: 40%;
        width: 40%;
    }
    #ih_4{
       
        font-size: 24px;
    }
    #ih_1{
         font-size: 29px;
    }
    #ih_2{
        font-size: 21px;
    }

    .Main_side{
        height: 90%;
        display: flex;
        min-width: 60%;
        width: 60%;
        flex-wrap: wrap;
        overflow-y: scroll;
    }
    .intro,.myproj,.getinfo,.about{
        min-width: 55%;
        font-size: 20px;
            width: 75%;
        height: 35%;
    }
    .seprate{
        font-size: 12px;
    }
}


@media (max-width:375px){
    .intro,.myproj,.getinfo,.about{
        min-width: 55%;
        font-size: 16px;
            width: 95%;

    }
}
@media (max-width:2039px){
     .Main_side{
       
        height: 88%;
        /* max-height: 90%; */
        display: flex;
        min-width: 60%;
        width: 70%;
        flex-wrap: wrap;
        overflow-y: scroll;
    }
} 
@media (max-width:1097px) {
    .Image{
          min-width: 30%;
        width: 30%;
    }
    .Main_side{
       
        height: 95%;
        /* max-height: 90%; */
        display: flex;
        min-width: 60%;
        width: 70%;
        flex-wrap: wrap;
        overflow-y: scroll;
    }
    .intro,.myproj,.getinfo,.about{
        min-width: 45%;
        font-size: 20px;
            width: 45%;
        height: 35%;
    }
    .seprate{
        font-size:16px;
    }
    #ih_4{
       
        font-size: 28px;
    }
    #ih_1{
         font-size: 33px;
    }
    #ih_2{
        font-size: 24px;
    }

}

